import React from "react";

const Input = ({ 
  className = "", 
  type = "text",
  placeholder = "",
  ...props 
}) => {
  const baseClasses = "flex h-10 w-full rounded-md border border-gray-300 bg-white px-3 py-2 text-sm placeholder:text-gray-400 focus:outline-none focus:ring-2 focus:ring-green-500 focus:border-transparent disabled:cursor-not-allowed disabled:opacity-50";
  
  const classes = `${baseClasses} ${className}`;
  
  return (
    <input
      type={type}
      className={classes}
      placeholder={placeholder}
      {...props}
    />
  );
};

export default Input;
